  align-items: center;
  margin: 60px 0;
  display: flex;
}

.movies-container {
  max-width: 1200px;
  grid-gap: 30px;
  grid-template-columns: repeat(3, 1fr);
  justify-content: center;
  align-items: center;
  justify-items: center;
  margin: 0;
  display: grid;
}

.info__title {
  font-family: Roboto, sans-serif;
}

.info__genre {
  color: #ff6b08;
  font-family: Roboto, sans-serif;
